:root {
  --bg-color: #E7EBF0;
  --fg-color: #E7EBF0;
}

@keyframes disappear-down {
  0% {
    height: 0;
    padding: 0;
  }
  100% {
    
  }
}

@keyframes appear-up {
  0% {
    height: 0;
    padding: 0;
  }
  100% {
    height: 100%;
    padding: 0;
  }
}

.hide-me {
  animation-name: disappear-down;
  animation-duration: 1s;
  animation-iteration-count: 1;
  overflow: hidden;
  animation-fill-mode: forwards;
}

.show-me {
  animation-name: appear-up;
  animation-duration: 1s;
  animation-iteration-count: 1; 
  overflow: hidden;
  animation-fill-mode: backwards;
}

.App-main {
  display: flex;
  align-items: top;
  justify-content: center;
  flex-direction: column;
  font-size: calc(5px + 2vmin);
  color: black;
  margin: 0;
  margin-bottom: 15px;
}

.App-main-row {
  display: flex;
  flex-direction: row;
  padding: 1em;
}

.App-main-row > div:first-child {
  display: flex;
  flex-direction: column;
  padding: 15px;
}

.App-main-slider {
  max-width: 47vw;
}

.App-cardRow {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 4vh;
  margin-bottom: 4vh;
}

.card-rank-icon {
  width: 30px;
}

.chart-demo-image {
  width: 100%;
}